* {
  box-sizing: border-box;
}

:root {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition-duration: 1s;
}

.people {
  --maxwidth: 430px;
  --column-count: 2;
  max-width: var(--maxwidth);
  grid-template-columns: repeat(var(--column-count), 1fr);
  gap: 40px 30px;
  margin: 0 auto;
  padding-top: 30px;
  padding-bottom: 100px;
  display: grid;
}

@media (width >= 500px) {
  .people {
    --column-count: 2;
  }
}

@media (width >= 1000px) {
  .people {
    --column-count: 4;
    --maxwidth: 890px;
  }
}

.people .person {
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  padding: 12px;
  display: flex;
}

.people .person .photo {
  width: 200px;
  height: 200px;
  position: relative;
}

.people .person .photo .rank {
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 32px;
  position: absolute;
  top: 50%;
  bottom: 50%;
  right: 10px;
  transform: translate(0, -50%);
}

.people .person .details .name {
  color: #2f4f4f;
  margin-top: 8px;
  font-size: 20px;
  line-height: 23px;
}

.people .person .details .extra {
  text-align: center;
  margin-top: 9px;
  font-size: 18px;
  line-height: 18px;
  display: none;
}

.people .person .details .extra .sport:after {
  content: ",";
}

.people .person .details .extra .pay:before {
  content: "$";
}

.people .person .details .extra .pay:after {
  content: "M";
}

.people .person:hover {
  background-color: #eee;
  scale: 1.2;
}

.people .person:hover img {
  border-radius: 60px;
}

.people .person:hover .extra {
  display: block;
}
